import '../championsList/championList.scss';
import ChampionsService from '../../services/ChampionsService';
import Loading from '../loadingKata/loadingKata';
import React, { Component } from 'react';
import VersionService from '../../services/VersionService';
class ChampionList extends Component {
    state = {
        champions: [],
        loading: true,
        error: false,
        searchedChamp: "",
        selectedLanguage: '',
        loaded: false,
        filters: [`Tank`, `Marksman`, `Assassin`]
    }

    championService = new ChampionsService();

    versionService = new VersionService();
    componentDidMount() {
        // this.updateChamp();
    }
    componentDidUpdate(prevProps) {
        if (this.props.selectedLanguage !== prevProps.selectedLanguage) {
            this.updateChamp();
        }
        // if (this.props.loaded !== prevProps.loaded) {
        //     // this.updateLoadState();
        // }
        if (this.props.version !== prevProps.version) {
            this.updateChamp();
        }
    }
    updateChamp = () => {
        const { selectedLanguage, version } = this.props;
        if (!version) {
            return;
        }
        if (!selectedLanguage) {
            return;
        }
        this.championService
            .getAllChampions(selectedLanguage, version)
            .then(this.onChampLoaded)
            .catch(this.onError)
        this.setState({
            loading: false
        })
    }
    shouldComponentUpdate(nextProps, nextState) {
        if (this.props.searchedChamp !== nextProps.searchedChamp) {
            this.setState({
                searchedChamp: nextProps.searchedChamp
            });
        }
        if (this.props.selectedLanguage !== nextProps.selectedLanguage) {
            this.setState({
                selectedLanguage: nextProps.selectedLanguage
            });
        }
        if (this.props.loaded !== nextProps.loaded) {
            this.setState({
                loaded: nextProps.loaded
            });
        }
        return true;
    }

    onLangError = () => {
        this.setState({
            selectedLanguage: 'en_US',
        })
    }
    onChampLoaded = (champions) => {
        this.setState({
            champions,
            loading: false,
        })
    }

    importAll(r) {
        let images = {};
        // eslint-disable-next-line
        r.keys().map((item) => { images[item.replace('./', '')] = r(item); });
        return images;
    }

    filterSetting = (filters, searchedChamp) => {
        let bool = false;
        // eslint-disable-next-line
        filters.map(item => {
            // eslint-disable-next-line
            if (searchedChamp.toLowerCase() === item.toLowerCase()) {
                bool = true
            }
        })
        return bool;

    }

    championFilter = (champions, searchedChamp) => {
        if (!searchedChamp === undefined || !searchedChamp === null || searchedChamp.replace(/\s/g, "").length > 1) {
            // eslint-disable-next-line
            const filteredChampions = champions.filter((item) => {
                if (item.name.toLowerCase().includes(searchedChamp.toLowerCase())) {
                    return item
                }
            })
            return filteredChampions;
        } else {
            return champions;
        }

    }

    champItems(champions) {
        // const images = this.importAll(require.context("../../img/champions/championsIcons/", false, /\.(png|jpe?g|svg)$/));
        const { version } = this.props;
        const elements = champions.map((item) => {
            return (
                <li
                    className="champions__item"
                    key={item.id}
                    tabIndex="0"
                    data-for={item.nameId}
                    onClick={() => {
                        this.props.onChampSelected(item.nameId);
                    }}
                    onKeyPress={(e) => {
                        if (e.key === ' ' || e.key === "Enter") {
                            this.props.onChampSelected(item.nameId);
                        }
                    }}>
                    <img
                        className='champions__image'
                        src={`https://ddragon.leagueoflegends.com/cdn/${version}/img/champion/${item.nameId}.png`}
                        // src={images[`${item.nameId + ".png"}`]}
                        alt={item.name}
                    />
                    <p
                        className='champions__tooltip'
                    >{item.name}</p>
                    {/* <p
                        ref={ref => this.fooRef = ref}
                        data-tip='tooltip'
                    >{item.name}, {item.title}</p> */}
                </li>
            )
        })

        return (
            <ul className="champions__list">
                {elements}
            </ul>
        )
    }
    render() {
        const { champions, loading, error, searchedChamp, loaded } = this.state;
        const items = this.champItems(this.championFilter(champions, searchedChamp))
        const katarina = loading ? <Loading /> : null;
        const content = !(loading || error) ? items : null;
        const hiding = loaded ? "champions--hide" : null;
        return (
            <div className={`champions + ${hiding}`}>
                {katarina}
                {content}
            </div>
        )
    }
}

export default ChampionList;
