import '../championSearch/championSearch.scss';
// import ChampionsService from '../../services/ChampionsService';
import React, { Component } from 'react';
// import Loading from '../loading/loading';
class ChampionSearch extends Component {
    state = {
        searchedChamp: ''
    }
    onChampSearch = (e) => {
        const searchedChamp = e.target.value;
        this.setState({ searchedChamp })
        this.props.onChampSearch(searchedChamp);
    }

    render() {
        return (
            <div className='championSearch'>
                <input type="text"
                    className="championSearch__input"
                    placeholder="Champion search...  e.g. miss fortune"
                    value={this.state.searchedChamp}
                    onChange={this.onChampSearch}
                />
            </div>
        )
    }
}

export default ChampionSearch;