class SkillsService {
    getResource = async (url) => {
        let res = await fetch(url);

        if (!res.ok) {
            throw new Error(`Could not fetch ${url}, status: ${res.status}`)
        }

        return await res.json();
    }

    getSkillStats = async (champName) => {
        const skillToolTip = await this.getResource(`https://raw.communitydragon.org/latest/game/data/characters/${champName.toLowerCase()}/${champName.toLowerCase()}.bin.json`)
        // const test = await this.getResource(`https://raw.communitydragon.org/latest/game/data/menu/fontconfig_en_us.txt.json`)
        // let passiveTooltip = test.entries[`buff_${champName.toLowerCase()}p_tooltip`]
        // let a = test.entries.buff_ahrip_tooltip;
        if (champName === "Fiddlesticks") { champName = "FiddleSticks" }
        let keys = Object.keys(skillToolTip).filter((key) => key.includes(`Characters/${champName}/Spells/`));
        let skills = {};
        keys.forEach(element => {
            skills[element] = skillToolTip[element]
        });

        return skills;
    }
}

export default SkillsService;