class DetailedStatsService {
    getResource = async (url) => {
        let res = await fetch(url);

        if (!res.ok) {
            throw new Error(`Could not fetch ${url}, status: ${res.status}`)
        }

        return await res.json();
    }

    getDetailedStats = async (champName, version) => {
        const champData = await this.getResource(`https://ddragon.leagueoflegends.com/cdn/${version}/data/en_US/champion/${champName}.json`);
        return champData.data[`${champName}`].stats;
        // const championData = Object.keys(champData.data).map((key) => champData.data[key]);
        // return championData.map(this._transformChampionData);

    }

    _transformChampion = (champ) => {
        return {
            id: champ.key,
            name: champ.name,
            nameId: champ.id,
            title: champ.title,
            tags: champ.tags
        }
    }
}

export default DetailedStatsService;