import '../championSkins/championSkins.scss';
import SkinsService from '../../services/SkinsService';
import React, { Component } from 'react';
import Loading from '../loadingKata/loadingKata';
import Error from '../errors/champInfoError';
import Tippy from '@tippyjs/react';
import SkinsBioService from '../../services/SkinsBioService';
class ChampionSkins extends Component {
    state = {
        loading: true,
        error: false,
        skins: [],
        champName: "",
        skinsBio: []
    }
    skinsService = new SkinsService();
    skinsBioService = new SkinsBioService();
    componentDidUpdate(prevProps) {
        if (this.props.selectedChamp !== prevProps.selectedChamp) {
            this.skinsStop();
            this.skinsUpdate();
        }
        if (this.props.selectedLanguage !== prevProps.selectedLanguage) {
            this.skinsUpdate();
        }
        return true;
    }
    skinsUpdate = () => {
        const { selectedChamp, selectedLanguage, version } = this.props;
        if (!selectedChamp) {
            return;
        }
        if (!selectedLanguage) {
            return;
        }
        if (!version) {
            return;
        }
        this.skinsService
            .getChampionsSkins(selectedChamp, selectedLanguage, version)
            .then(this.skinsSetup)
            .catch()
    }
    skinsStop() {
        if (this.props.selectedChamp === null) {
            this.setState({
                loading: true
            })
        }
    }

    skinsSetup = (skins, champName) => {
        const { selectedLanguage, selectedChamp } = this.props;
        this.setState({
            skins: skins.skins,
            champName: skins.champName,
            loading: false
        })
        this.skinsBioLoad(skins, selectedLanguage, selectedChamp)
    }
    skinsBioLoad = (id, language, champName) => {
        this.skinsBioService
            .getSkinBio(id, language, champName)
            .then(this.skinsBioSetup)
            .catch()
    }
    skinsBioSetup = (skinsBio) => {
        this.setState({
            skinsBio: skinsBio
        })
    }
    skinsBioTooltip = (id, title) => {
        const { skinsBio } = this.state;
        const description = !(skinsBio[`${id}`] === undefined || skinsBio[`${id}`] === null) ? skinsBio[`${id}`].description : null;
        if (description === undefined || description === null) {
            return (
                <div className="championSkins__bioToolTip">
                    <div className="championSkins__bioTitle">{title}</div>
                    <div className='championSkins__bioDecor'></div>
                    No description for this skin :(
                </div>
            )
        } else {
            return (
                <div className="championSkins__bioToolTip">
                    <div className="championSkins__bioTitle">{title}</div>
                    <div className='championSkins__bioDecor'></div>
                    {description}
                </div>
            )
        }

    }

    skinsLoading = (skins, champId, champName) => {
        const elements = skins.map((item, i) => {
            const skinName = item.name === "default" ? champName : item.name;
            if (champId === "Fiddlesticks") {
                champId = "FiddleSticks"
            }
            return (
                <Tippy
                    content={this.skinsBioTooltip(item.id, skinName, champName)}
                    placement="bottom"
                    animation="scale-subtle"
                    arrow={false}
                    duration={50}
                    delay={[75, 0]}
                    key={i}
                >
                    <li
                        className="championSkins__item"
                        key={item.num}
                    // onMouseEnter={() => this.skinsBioLoad(item.id, champName)}
                    >
                        <div className='championSkins__skinName'>{skinName}</div>
                        <img className="championSkins__image" src={`https://ddragon.leagueoflegends.com/cdn/img/champion/splash/${champId}_${item.num}.jpg`} alt={`${champName} skin ${i}`} tabIndex="0"></img>

                    </li>
                </Tippy>
            )
        })

        return elements;
    }

    render() {
        const { loading, error, skins, champName } = this.state;
        const { selectedChamp } = this.props;
        const active = (selectedChamp === "" || selectedChamp === null || selectedChamp === undefined) ? "championSkins" : "championSkins championSkins--active";
        const items = selectedChamp !== null ? this.skinsLoading(skins, selectedChamp, champName) : null;
        const katarina = loading ? <Loading /> : null;
        const errorChampSkins = error ? <Error /> : null;
        const content = !(loading || error) ? items : null;
        return (
            <div className={active}>
                <div className='championSkins__wrapper'>
                    <div className="championSkins__title">Skins</div>
                    <ul className='championSkins__list'>
                        {katarina}
                        {errorChampSkins}
                        {content}
                    </ul>
                </div>
            </div>
        )
    }
}

export default ChampionSkins;