class SkinsBioService {
    getResource = async (url) => {
        let res = await fetch(url);

        if (!res.ok) {
            throw new Error(`Could not fetch ${url}, status: ${res.status}`)
        }

        return await res.json();
    }
    getSkinBio = async (skinIds, language, champName) => {
        const lang = language === "en_US" ? "default" : language.toLowerCase();
        const skinBioData = await this.getResource(`https://raw.communitydragon.org/latest/plugins/rcp-be-lol-game-data/global/${lang}/v1/skins.json`);
        let skinsBio = {};

        skinIds.skins.forEach(element => {
            skinsBio[element.id] = skinBioData[`${element.id}`]

        });
        return skinsBio;

    }
}

export default SkinsBioService;