class ChampionsService {
    // version = "12.1.1";
    // language = "en_US"
    getResource = async (url) => {
        let res = await fetch(url);

        if (!res.ok) {
            throw new Error(`Could not fetch ${url}, status: ${res.status}`)
        }

        return await res.json();
    }

    getAllChampions = async (language, version) => {
        // const champ = await this.getResource('https://raw.communitydragon.org/latest/game/data/characters/aatrox/aatrox.bin.json')
        const champs = await this.getResource(`https://ddragon.leagueoflegends.com/cdn/${version}/data/${language}/champion.json`);
        const _champions = Object.keys(champs.data).map((key) => champs.data[key]);
        // console.log(champ)
        return _champions.map(this._transformChampion)

    }
    getChampionsStats = async (champName, language, version) => {
        const champData = await this.getResource(`https://ddragon.leagueoflegends.com/cdn/${version}/data/${language}/champion/${champName}.json`);
        const championData = Object.keys(champData.data).map((key) => champData.data[key]);
        return championData.map(this._transformChampionData);

    }

    _transformChampion = (champ) => {
        return {
            id: champ.key,
            name: champ.name,
            nameId: champ.id,
            title: champ.title,
            tags: champ.tags
        }
    }
    _transformChampionData = (championData) => {
        return {
            id: championData.key,
            name: championData.name,
            nameId: championData.id,
            title: championData.title,
            lore: championData.lore,
            attack: championData.info.attack,
            defense: championData.info.defense,
            magic: championData.info.magic,
            difficulty: championData.info.difficulty,
            spells: championData.spells,
            passiveSpell: championData.passive,
        }
    }
}

export default ChampionsService;