class SkinsService {
    getResource = async (url) => {
        let res = await fetch(url);

        if (!res.ok) {
            throw new Error(`Could not fetch ${url}, status: ${res.status}`)
        }

        return await res.json();
    }
    getChampionsSkins = async (champName, language, version) => {
        const champData = await this.getResource(`https://ddragon.leagueoflegends.com/cdn/${version}/data/${language}/champion/${champName}.json`);
        // const champData = await this.getResource(`http://ddragon.leagueoflegends.com/cdn/${version}/data/${language}/champion/${champName}.json`);
        const skins = champData.data[`${champName}`].skins;
        const championName = champData.data[`${champName}`].name;
        return this._transformData(skins, championName);

    }

    _transformData = (skins, championName) => {
        return {
            skins: skins,
            champName: championName
        }
    }
}

export default SkinsService;