import '../languageChanger/languageChanger.scss';
import LanguageService from '../../services/LanguageService';
import React, { Component } from 'react';
// import Loading from '../loading/loading';
class LanguageChanger extends Component {
    state = {
        defaultLanguage: 'en_US',
        languageData: '',
        savedLanguage: '',
        selectedLanguage: '',
        loading: true
    }
    languageService = new LanguageService()
    componentDidMount() {
        this.updateLang();
    }
    updateLang = () => {
        this.languageService
            .getAllLanguages()
            .then(this.onLanguagesLoaded)
            .catch(this.setLanguageOnError)
    }
    setLanguageOnError() {
        this.setState({
            selectedLanguage: 'en_US'
        })
    }
    onLanguagesLoaded = (languageData) => {
        let savedLanguage;
        if (localStorage.getItem('Language') === null) {
            savedLanguage = "";
        } else {
            savedLanguage = localStorage.getItem('Language');
        }

        this.setState({
            languageData,
            savedLanguage: savedLanguage === null || savedLanguage.length > 1 ? savedLanguage : '',
            loading: false,
        })
        if (savedLanguage.length > 1) {
            this.props.onSelectedLanguage(savedLanguage);
        }

    }
    onChange = (event) => {
        this.setState({
            selectedLanguage: event.target.value,
            savedLanguage: event.target.value
        })
        this.props.onSelectedLanguage(event.target.value);
        localStorage.setItem('Language', event.target.value);
    }

    languages(languageData) {
        const { savedLanguage, defaultLanguage } = this.state;
        const languages = (languageData === null || languageData === undefined) ? defaultLanguage : languageData;
        const elements = languages.map(item => {
            if (item.languageData === "id_ID") {
                return null;
            }
            return (
                <option
                    className='languageChanger__option'
                    value={item.languageData}
                    key={item.languageData}
                >
                    {item.languageData.slice(3, 5)}
                </option>
            )
        });

        return (
            <select className='languageChanger__selection' value={savedLanguage} onChange={this.onChange}>
                {elements}
            </select>
        )
    }


    render() {
        const { languageData, loading } = this.state;
        const langs = loading ? null : this.languages(languageData);
        return (
            <div className='languageChanger'>
                {langs}
            </div>
        )
    }
}

export default LanguageChanger;