import './App.scss';
import React, { Component } from 'react';
import ChampionsList from '../championsList/championList';
import ChampionsInfo from '../championInfo/championInfo';
import ChampionSearch from '../championSearch/championSearch';
import ChampionSkins from '../championSkins/championSkins'
import LanguageChanger from '../languageChanger/languageChanger';
import VersionService from '../../services/VersionService';

class App extends Component {

  state = {
    selectedChamp: null,
    searchedChamp: '',
    selectedLanguage: '',
    loaded: false,
    version: '',
    versions: [],
    background: null
  }
  versionService = new VersionService();
  onChampSelected = (id) => {
    this.setState({
      selectedChamp: id,
      loaded: true
    })
  }
  componentDidMount = () => {
    this.versionUpdate()
  }

  versionUpdate = () => {
    this.versionService
      .getVersion()
      .then(this.onVersionLoaded)
  }
  onVersionLoaded = (versions) => {

    this.setState({
      version: versions[0],
      versions: versions
    })
  }

  onChampDisabled = () => {
    this.setState({
      selectedChamp: null,
      loaded: false
    })
  }
  onChampSearch = (searchedChamp) => {
    this.setState({ searchedChamp });
  }
  onSelectedLanguage = (selectedLanguage) => {
    this.setState({ selectedLanguage })
  }


  render() {
    const { searchedChamp, selectedLanguage, loaded, version, selectedChamp } = this.state;
    const champBack = selectedChamp === "Fiddlesticks" ? "FiddleSticks" : selectedChamp;
    const backStyle = selectedChamp ? { backgroundImage: `url('https://ddragon.leagueoflegends.com/cdn/img/champion/splash/${champBack}_0.jpg')`, opacity: 1 } : null;
    return (
      <div className="App">
        <div className="App__champBG" style={backStyle}></div >
        <header className="App__header">
          <div className="App__topPanel">
            <ChampionSearch onChampSearch={this.onChampSearch} />
            <LanguageChanger onSelectedLanguage={this.onSelectedLanguage} />
          </div>
          <ChampionsList
            searchedChamp={searchedChamp}
            loaded={loaded}
            onChampSelected={this.onChampSelected}
            selectedLanguage={selectedLanguage}
            version={version}
          />
          <ChampionsInfo
            selectedChamp={this.state.selectedChamp}
            onChampDisabled={this.onChampDisabled}
            selectedLanguage={selectedLanguage}
            version={version}
          />
          <ChampionSkins
            selectedChamp={this.state.selectedChamp}
            onChampDisabled={this.onChampDisabled}
            selectedLanguage={selectedLanguage}
            version={version}
          />
        </header>
      </div>
    );
  }

}

export default App;
