import '../championInfo/championInfo.scss';
import ChampionsService from '../../services/ChampionsService';
import React, { Component } from 'react';
import Loading from '../loadingKata/loadingKata';
import Error from '../errors/champInfoError';
import Tippy from '@tippyjs/react';
import 'tippy.js/themes/light.css';
import reactStringReplace from 'react-string-replace';
import SkillsService from '../../services/SkillsService';
import DetailedStatsService from '../../services/DetailedStatsService';

class ChampionInfo extends Component {
    state = {
        champion: null,
        loading: true,
        error: false,
        skills: null,
        stats: null,
        statsStyle: false,
        level: 1
    }
    colors = {
        phyDmg: "championInfo__phyDmg",
        mDmg: "championInfo__mDmg",
        spd: "championInfo__spd",
        healing: "championInfo__healing",
        trueDmg: "championInfo__trueDmg",
        status: "championInfo__status",
        scaleAD: "championInfo__scaleAD",
        scaleAP: "championInfo__scaleAP",
        active: "championInfo__active",
        passive: "championInfo__passive",
        recast: "championInfo__recast",
        br: "championInfo__br"
    }

    skillsTooltipKeys = {
        phyDmg: /<physicalDamage>(.*?)<\/physicalDamage>/g,
        mDmg: /<magicDamage>(.*?)<\/magicDamage>/g,
        spd: /<speed>(.*?)<\/speed>/g,
        healing: /<healing>(.*?)<\/healing>/g,
        trueDmg: /<trueDamage>(.*?)<\/trueDamage>/g,
        status: /<status>(.*?)<\/status>/g,
        scaleAD: /<scaleAD>(.*?)<\/scaleAD>/g,
        scaleAP: /<scaleAP>(.*?)<\/scaleAP>/g,
        active: /<spellActive>(.*?)<\/spellActive>/g,
        passive: /<spellPassive>(.*?)<\/spellPassive>/g,
        recast: /<recast>(.*?)<\/recast>/g,
        br: /<br \/>(.*?)<br \/>/g
    }
    champStatsDict = {
        0: {
            text: "Attack",
            base: "attackdamage",
            scale: "attackdamageperlevel",

        },
        1: {
            text: "Attack Speed",
            scale: "attackspeedperlevel",
            base: "attackspeed"
        },
        2: {
            text: "Attack Range",
            base: "attackrange",
            scale: null
        },
        3: {
            text: "Hit Points",
            base: "hp",
            scale: "hpperlevel",

        },
        4: {
            text: "HP Regen",
            base: "hpregen",
            scale: "hpregenperlevel"
        },
        5: {
            text: "Mana Points",
            base: "mp",
            scale: "mpperlevel",

        },
        6: {
            text: "MP Regen",
            base: "mpregen",
            scale: "mpregenperlevel"
        },

        7: {
            text: "Armor",
            base: "armor",
            scale: "armorperlevel",

        },
        8: {
            text: "Magic Resistance",
            base: "spellblock",
            scale: "spellblockperlevel",
        },
        9: {
            text: "Movement Speed",
            base: "movespeed",
            scale: null
        }
    }


    championService = new ChampionsService();
    skillsService = new SkillsService();
    detailedStatsService = new DetailedStatsService();

    componentDidMount() {
        this.updateChamp();
    }
    componentDidUpdate(prevProps) {
        if (this.props.selectedChamp !== prevProps.selectedChamp) {
            this.updateChamp();
        }
        if (this.props.selectedLanguage !== prevProps.selectedLanguage) {
            this.updateChamp();
        }
        return true;
    }
    updateChamp = () => {
        const { selectedChamp, selectedLanguage, version } = this.props;
        if (!selectedChamp) {
            return;
        }
        if (!selectedLanguage) {
            return;
        }
        if (!version) {
            return;
        }

        this.onChampLoading();

        this.championService
            .getChampionsStats(selectedChamp, selectedLanguage, version)
            .then(this.onChampLoaded)
            .catch(this.onError)
    }
    onChampLoading = (champion) => {
        this.setState({
            champion,
            error: false,
            loading: true
        })
    }
    onError = () => {
        this.setState({
            loading: false,
            error: true
        })
    }
    onChampLoaded = (champion) => {
        this.setState({
            champion,
            loading: false
        })
        this.stats()
    }
    importAll(r) {
        let images = {};
        // eslint-disable-next-line
        r.keys().map((item) => { images[item.replace('./', '')] = r(item); });
        return images;
    }

    toolTipLoad = (champId, item) => {
        this.skillsService
            .getSkillStats(champId)
            .then(this.onToolTipLoad)
            .catch()
    }
    onToolTipLoad = (skills) => {
        this.setState({
            skills: skills
        })
    }
    levelChange = (event) => {
        this.setState({
            level: event.target.value
        })
    }

    skills = {
        e2: "e2",
        totaldamage: "totaldamage"
    }

    closing = () => {
        this.setState({
            loading: true,
            skills: null
        });
        this.props.onChampDisabled()
    }

    tooltipDesc = (i, res, num, match, skillName) => {
        // console.log(res)
        // const skills = this.skills;
        // const spell = this.state.skills;
        // if (spell !== null) {
        //     Object.keys(skills).forEach((key) => {
        //         res = reactStringReplace(res, skills[key], (match, i, num) => (
        //             console.log(res)));
        //     });
        // }
        const colors = this.colors;
        let classColor = match in colors ? colors[match] : null;
        return (
            <span key={num + skillName + i} className={classColor}>{res}</span>
        )

    }
    toolTipContent = (item, skillName) => {
        let skillsTooltipKeys = this.skillsTooltipKeys;
        let tooltipEdited = item;
        let keyReact = 0;
        Object.keys(skillsTooltipKeys).forEach((key) => {
            tooltipEdited = reactStringReplace(tooltipEdited, skillsTooltipKeys[key], (match, i, num) => (
                this.tooltipDesc(keyReact++, match, num, key, skillName)));
        })

        return (
            <div className='championInfo__tooltip'>
                <div className="championInfo__tooltip-desc">
                    <div className="championInfo__tooltip--title">{skillName}</div>
                    {tooltipEdited}
                </div>
            </div>
        )
    }
    Right(champion) {
        const { attack, defense, magic, difficulty } = champion;
        const check = attack + defense + magic + difficulty === 0 ? true : false;

        return (
            <div className="championInfo__sector championInfo__sector--right">
                <div className="championInfo__statsTitle">Stats:</div>
                {check ? <div> No data in riot API :(</div> : null}
                <div className="championInfo__stats championInfo__stats--attack">

                    <div className="championInfo__statsName">Attack:</div>
                    {check ? <div></div> : React.Children.toArray(
                        Array.from(Array(attack)).map(() => <div className="championInfo__statsPoint"></div>)
                    )}

                </div>
                <div className="championInfo__stats championInfo__stats--defense">
                    <div className="championInfo__statsName">Defense:</div>
                    {check ? <div></div> : React.Children.toArray(
                        Array.from(Array(defense)).map(() => <div className="championInfo__statsPoint"></div>)
                    )}
                </div>
                <div className="championInfo__stats championInfo__stats--magic">
                    <div className="championInfo__statsName">Magic:</div>
                    {check ? <div></div> : React.Children.toArray(
                        Array.from(Array(magic)).map(() => <div className="championInfo__statsPoint"></div>)
                    )}
                </div>
                <div className="championInfo__stats championInfo__stats--difficulty">
                    <div className="championInfo__statsName">Difficulty:</div>
                    {check ? <div></div> : React.Children.toArray(
                        Array.from(Array(difficulty)).map(() => <div className="championInfo__statsPoint"></div>)
                    )}
                </div>
            </div>
        )
    }
    Left(champion) {
        const srcName = champion.nameId === "Fiddlesticks" ? "FiddleSticks" : champion.nameId;
        // const images = this.importAll(require.context("../../img/champions/loading/", false, /\.(png|jpe?g|svg)$/));
        return (
            <div className="championInfo__sector championInfo__sector--left">
                <img className="championInfo__loading"
                    // src={images[`${srcName}_0.jpg`]}
                    src={`https://ddragon.leagueoflegends.com/cdn/img/champion/loading/${srcName}_0.jpg`}
                    width="90%"
                    alt="" />
            </div>
        )
    }
    stats = () => {
        const { selectedChamp, version } = this.props;
        if (!selectedChamp) {
            return;
        }
        if (!version) {
            return;
        }
        this.detailedStatsService
            .getDetailedStats(selectedChamp, version)
            .then(this.setStats)
            .catch()
    }
    setStats = (stats) => {
        this.setState({
            stats: stats
        })
    }
    switchStats = () => {
        const styleValue = this.state.statsStyle
        this.setState({
            statsStyle: !styleValue
        })
    }
    statsList(stats) {
        if (stats === null) {
            return
        }
        const { statsStyle, level } = this.state;
        const statsDict = Object.values(this.champStatsDict);
        const setStyleStat = statsStyle ? "championInfo__detailedStatsList" : "championInfo__detailedStatsList  championInfo__detailedStatsList--disabled";
        const elements = statsDict.map((item => {
            const base = stats[`${item.base}`];
            const scale = item.scale === null ? 0 : stats[`${item.scale}`];
            const formula = item.text === "Attack Speed" ? base * (scale * (level - 1) * (0.7025 + 0.0175 * (level - 1)) / 100) + base : base + scale * (level - 1) * (0.7025 + 0.0175 * (level - 1));
            const percents = item.text === "Attack Speed" ? "" : "";
            const result = item.text === "Attack Speed" ? Number(Math.round(formula + 'e3') + 'e-3') : Number(Math.round(formula + 'e2') + 'e-2');

            return (
                <li key={item.text} className='championInfo__detailedStatsItem'>
                    <span className='championInfo__detailedStatsItem-Left'>{item.text} : {base}
                        <span className='championInfo__detailedStatsItem-Left--dec'>(per level : {scale} )</span>
                    </span>
                    <span className='championInfo__detailedStatsItem-Right'>Total: {result}{percents}</span>
                </li>
            )
        }))
        return (
            <ul className={setStyleStat}>
                {elements}
            </ul>
        )
    }
    Center(champion) {
        const { name, lore, title, passiveSpell, spells, nameId } = champion;
        const { stats } = this.state;
        const { version } = this.props;
        const { statsStyle } = this.state;
        // const images = this.importAll(require.context("../../img/champions/spell/", false, /\.(png|jpe?g|svg)$/));
        const selectorStyle = statsStyle ? "championInfo__levelSelectorWrapper" : "championInfo__levelSelectorWrapper championInfo__levelSelectorWrapper--disabled";
        const spellsTotal = [passiveSpell, ...spells]
        const elements = spellsTotal.map((item, i) => {

            const spellSrc = i === 0 ? `https://ddragon.leagueoflegends.com/cdn/${version}/img/passive/${item.image.full}` : `https://ddragon.leagueoflegends.com/cdn/${version}/img/spell/${item.image.full}`;
            let tooltip = item.tooltip !== undefined && item.tooltip !== null ? item.tooltip : item.description;
            let key = !item.id === null || !item.id === undefined ? item.id : item.name;
            return (
                <Tippy
                    content={this.toolTipContent(tooltip, item.name, name)}
                    placement="bottom"
                    animation="scale-subtle"
                    arrow={false}
                    duration={200}
                    delay={[75, 0]}
                    key={key}
                >
                    <li className="championInfo__skillsListItem" tabIndex={0}>
                        <img
                            className="championInfo__skill"
                            // src={images[`${spellSrc}`]}
                            src={spellSrc}
                            alt={name + i + "Skill"}
                            onMouseEnter={() => this.toolTipLoad(nameId)} />
                        <p className='championInfo__skills'></p>
                    </li>
                </Tippy>
            )
        })
        const selectionElements = Array.from(Array(18).keys()).map((item => {
            return (
                <option className='championInfo__levelSelectorValue' value={item + 1} key={item + 1}>{item + 1}</option>
            )
        }))

        return (
            <>
                <div className="championInfo__sector championInfo__sector--center">
                    <h2 className="championInfo__title"
                    >{name}, {title}</h2>
                    <p className="championInfo__lore">
                        {lore}
                    </p>
                    <ul className="championInfo__skillsList">
                        {elements}
                    </ul>
                    <div className="championInfo__detailedStats">
                        <span
                            tabIndex={0}
                            className='championInfo__detailedStatsTitle'
                            onClick={() => this.switchStats()}
                            onKeyPress={(e) => {
                                if (e.key === ' ' || e.key === "Enter") {
                                    this.switchStats();
                                }
                            }}
                        >Detailed stats</span>
                        <div className={selectorStyle}>
                            <span className='championInfo__levelSelectorTitle'>Level : </span>
                            <select value={this.state.level} onChange={this.levelChange} className='championInfo__levelSelector'>

                                {selectionElements}
                            </select>
                        </div>

                        {this.statsList(stats)}
                    </div>
                </div></>
        )
    }



    render() {
        const { champion, loading, error } = this.state;
        const { selectedChamp } = this.props;
        const active = (selectedChamp === "" || selectedChamp === null || selectedChamp === undefined) ? "championInfo" : "championInfo championInfo--active";
        const katarina = loading ? <Loading /> : null;
        const errorChampInfo = error ? <Error /> : null;
        const centerContent = !(loading || error || !champion) ? this.Center(champion[0]) : null;
        const leftContent = !(loading || error || !champion) ? this.Left(champion[0]) : null;
        const rightContent = !(loading || error || !champion) ? this.Right(champion[0]) : null;

        return (

            <div className={active}>
                <div className="championInfo__wrapper"
                    onKeyPress={(e) => {
                        if (e.key === "Esc") {
                            this.props.onChampDisabled();
                        }
                    }}>
                    <div className="championInfo__closeBtn"
                        tabIndex="0"
                        onClick={() => this.closing()}
                        onKeyPress={(e) => {
                            if (e.key === ' ' || e.key === "Enter") {
                                this.closing();
                            }
                        }}
                    ></div>
                    {katarina}
                    {errorChampInfo}
                    {leftContent}
                    {centerContent}
                    {rightContent}
                </div>
            </div>
        )
    }
}

